

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@font-face
{
    font-family: based;
    src: url(font.ttf);
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body
{
    background-image: url('backpic.jpg');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    background-attachment: fixed;
    overflow-x: hidden;
    font-family: based;
    margin-bottom: 1cm;
}

#contact
{
    position: relative;
}

#navbar
{
    padding-top: 20px;
    padding-bottom: 15px;
    font-size: 25px;
    text-align: center;
    background-color: rgb(0,0,0,0.3);
    color: white;
    text-decoration: none;
    font-family: based;
    font-size: 20px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    width: 100%;
}

#contact1
{
    position: relative;
    top: 180px;
    border-radius: 5px;
    display: flex;
    margin-left: auto;
    margin-right: auto;
    width: 90%;
    background-color: rgb(0,0,0,0.5);
    align-items: center;
    margin-top: 2px;
}

.imgcontact
{
    width: 30%;
}

#htab:visited
{
    color: white;
    text-decoration: none;
}

#edtab:visited
{
    color: white;
    text-decoration: none;
}

#extab:visited
{
    color: white;
    text-decoration: none;
}

#htab:hover, #extab:hover, #edtab:hover
{
    background-color: rgba(99, 75, 7, 0.9);
    transition: background-color 1s linear;
    border-bottom: 1px solid white;
}

#htab:link, #extab:link, #edtab:link
{
    color: white;
    text-decoration: none;
}

#htab
{
    margin-left: 0;
    margin-right: 0;
    width: 100%;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 5%;
    padding-right: 5%;
}
  
#extab
{
    margin-left: 0;
    margin-right: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 5%;
    padding-right: 7%;
    width: 100%;
}

#edtab
{
    margin-left: 0;
    margin-right: 0;
    padding-top: 15px;
    padding-bottom: 15px;
    padding-left: 6.5%;
    padding-right: 6.5%;
    width: 100%;
}


#identify
{
    font-family: based;
    color: #f5f5f5;
    text-align: center;
    position: relative;
    top: 150px;
}

footer
{
    text-align: right;
    padding: 10px;
    background-color: rgb(0,0,0,0.3);
    margin-left: auto;
    margin-right: auto;
    width: 100%;
    color: white;
    position: relative;
    top: 400px;
    right: 10px;
    padding-bottom: 40px;
}

.imgs
{
    display: block;
    margin-left: auto;
    margin-right: auto;
    position: relative;
    top: 10px;
    width: 40%;
}

#education-list
{
    background-color: rgb(0,0,0,0.4);
    color: white;
    font-size: 20px;
    margin-top: 30px;
    position: relative;
    padding: 10px;
    border-radius: 12px;
    padding-top: 70px;
    padding-bottom: 70px;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    padding: 3%;
    padding-bottom: 4%;
}

.para
{
    font-size: 12px;
    position: relative;
    left: 20px;
    font-family: based;
    line-height: 20px;
}

#btn-sgpa
{
    margin-left: 6%;
    background-color: rgba(99, 75, 7, 1);
    font-family: based;
    color: white;
    width: 30%;
    padding-top: 10%;
    padding-top: 1%;
    padding-bottom: 1%;
    border: 1px solid white;
    border-radius: 5px;
}

#btn-sgpa:hover{
    background-color: black;
    transition: background-color 200ms linear;
}
.list
{
    font-size: 12px;
}

.imgse
{
    display: block;
    margin-left: auto;
    width: 100%;
    margin-right: auto;
}

#projects
{
    text-align: left;
    font-family: based;
    color: rgb(255,255,255,1);
    padding: 10px;
}

#experience
{
    background-color: rgb(0,0,0, 0.5);
    border-radius: 10px;
    margin-top: 30px;
    position: relative;
    padding-bottom: 20px;
    color: white;
    padding: 10px;
    margin-left: 2%;
    margin-right: 2%;
}

.pj
{
    background-color: rgba(0,0,0,0.7);
    margin: 5px;
    border: 2px solid black;
    padding: 10px;
    border-radius: 10px;
    color: white;
}

.links:visited
{
    color:blue;
    text-decoration: underline;
}

#applications
{
    display: none;
}

#web-apps
{
    display: none;
}  

#games
{
    display: none;
}

.pj-opener
{
    padding: 3%;
    border: 1px solid rgba(99, 75, 7);
    margin-top: 2%;
    border-radius: 5px;
}

.arrow-head
{
    text-align: right;
}

@media only screen and (max-width: 320px)
{

    #navbar
    {
        font-size: 12px;
    }

    #education-list
    {   
        margin-top: 20%;
        font-size: 12px;
    }

    .para
    {
        font-size: 10px;
    }

    #btn-sgpa
    {
        font-size: 9px;
        width: 40%;
        margin-left: 8.5%;
    }

    .list
    {
        font-size: 10px;
    }


    #projects
    {
        font-size: 19px;
    }

    #htab, #edtab, #extab{
        font-size: 16.5px;
    }

    .tabs
    {
        font-size: 17px;
    }
}

@media only screen and (min-width: 768px)
{

    #htab
    {
        margin-right: 150px;
    }

    #extab
    {
        margin-left: 150px;
    }

    #identify
    {
        top: 200px;
    }

    #htab, #edtab, #extab
    {
        margin-left: 0;
        margin-right: 0;
        padding-left: 12%;
        padding-right: 12%;
    }

    #contact
    {
        top: 80px;
    }

    .imgs
    {
        width: 20%;
    }

    .para
    {
        font-size: 15px;
    }

   #education-list
    {
        margin-top: 100px;
    }

    #btn-sgpa
    {
        margin-left: 3.4%;
    }

    .list
    {
        font-size: 15px;
    }

    #experience
    {
        margin-top: 12%;
        margin-left: auto;
        margin-right: auto;
        display: block;
        width: 90%;
    }
}

@media only screen and (min-width: 800px) and (min-height: 1100px)
{
    footer
    {
        top: 840px;
    }
    
    #contact
    {
        top: 200px;
    }

    .imgs
    {
        width: 20%;
    }

    #education-list
    {
        margin-top: 200px;
    }
}

@media only screen and (min-width: 1024px)
{
    footer
    {
        top: 260px;
    }

    

    #identify
    {
        top: 60px;
    }

    #htab, #edtab, #extab
    {
        margin-left: 0;
        margin-right: 0;
        padding-left: 12%;
        padding-right: 12%;
    }

    #contact
    {
        top: 10px;
    }

    .imgs
    {
        width: 12%;
    }

    #education-list
    {
        margin-top: 50px;
    }

    #btn-sgpa
    {
        margin-left: 2.3%;
    }

    #experience
    {
        margin-top: 2%;
        margin-left: auto;
        margin-right: auto;
        width: 65%;
    }
}

@media only screen and (min-width: 1280px)
{
    footer
    {
        top: 450px;
    }

    #htab
    {
        margin-right: 300px;
    }

    #extab
    {
        margin-left: 300px;
    }

    #identify
    {
        top: 100px;
    }

    #htab, #edtab, #extab
    {
        margin-left: 0;
        margin-right: 0;
        padding-left: 12%;
        padding-right: 12%;
    }

    #contact
    {
        top: 50px;
        margin-left: auto;
        margin-right: auto;
        display: block;
        width: 50%;

    }

    .imgs
    {
        width: 15%;
    }

    .imgcontact
    {
        width: 40%;
    }

    #btn-sgpa
    {
        margin-left: 1.75%;
        width: 11%;
    }

}

@media only screen and (min-height: 890px) and (max-width: 450px)
{

    #education-list
    {
        margin-top: 100px;
    }
}

